body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 10vh;
  overflow-x: hidden;

}
.form input {
    border: 2px solid black;
    padding: 10px 20px;
    border-radius: 10px;
}

.form button {
    margin: 10px;
    border: 2px solid white;
    padding: 5px 20px;
    background-color: royalblue;
    border-radius: 10px;
    color: white;
    font-weight: bold;
    box-shadow: 0 0 0px 2px royalblue
}
.weatherResult {
    margin: 20px;
    border: 2px solid black;
    border-radius: 20px;
    background-color: rgba(31, 149, 222, 0.5);
    padding: 10px;
    display: flex;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-grow: 1;
    width: 80vw;
    max-width: 600px;
    overflow-x: hidden;
}

.weatherResult p {
    margin: 10px;
    width: 100%;
    border: 2px solid black;
    padding: 10px;
    border-radius: 20px;
    background-color: white;
}

.weatherResult em {
    color: green;
}


.weatherIcon {
    width: 20%;
    height: 20%;
}
